import React from "react";
import Sidebar from "../../UI/Sidebar/Sidebar";
import "./Partner.css";
import AddStore from "./AddStore/AddStore";
import ViewStore from "./ViewStore/ViewStore";
import { Redirect, Route, Switch } from "react-router";
import { connect } from "react-redux";
import Home from "./home/home";
import PageShell from "../../UI/PageShell/PageShell";
import EachStore from "./ViewStore/EachStore/EachStore";
import Profile from "../Partner/Profile/Profile";
import AddOfferPartner from "./Offer/AddOfferPartner/AddOfferPartner";
import ViewOfferPartner from "./Offer/ViewOfferPartner/ViewOfferPartner";

const Partner = (props) => {
  // useEffect(() => {
  //   if (props.auth) {
  //     props.history.replace("/home");
  //   } else {
  //     props.history.replace("/auth");
  //   }
  // }, []);

  var partnerSidebar = [
    { name: "DASHBOARD", to: "/home", initialActive: true },
    {
      dropdown: {
        heading: "MY STORE",
        options: [
          { name: "ADD STORE", to: "/addstore" },
          { name: "View STORE", to: "/viewstore" },
        ],
      },
    },
    {
      dropdown: {
        heading: "OFFERS",
        options: [
          { name: "ADD OFFER", to: "/addoffer" },
          { name: "View OFFER", to: "/viewoffer" },
        ],
      },
    },
    { name: "LOGOUT", to: "/logout" },
    {
      component: (
        <div
          className="nav-logo"
          style={{
            backgroundImage:
              "url(" +
              (props.logo !== null
                ? props.logo
                : require("../../../assets/images/Default-Profile.png")) +
              ")",
          }}
        ></div>
      ),
      to: "/profile",
    },
  ];

  return (
    <div>
      <Sidebar
        showOnMobile={
          <div
            className="nav-logo-mobile"
            onClick={() => props.history.push("/profile")}
            style={{
              backgroundImage:
                "url(" +
                (props.logo !== null
                  ? props.logo
                  : require("../../../assets/images/Default-Profile.png")) +
                ")",
            }}
          ></div>
        }
        style={{ background: "transparent" }}
        routes={partnerSidebar}
        heading={
          <img
            src={require("../../../assets/images/EZWalkin-Logo.jpg")}
            alt="logo"
            // className="logo"
          />
        }
      />
      <br />
      <Switch>
        <Route path="/profile" component={PageShell(Profile)} />
        <Route path="/home" component={PageShell(Home)} />
        <Route path="/addstore" component={PageShell(AddStore)} />
        <Route path="/viewstore" exact component={PageShell(ViewStore)} />
        <Route path="/viewstore/:id" component={PageShell(EachStore)} />
        <Route path="/addoffer" component={PageShell(AddOfferPartner)} />
        <Route path="/Viewoffer" component={PageShell(ViewOfferPartner)} />
        <Redirect to={props.auth ? "/home" : "/auth"} />
      </Switch>
    </div>
  );
};

const mapStateToProps = (state) => {
  return {
    auth: state.login.token !== null,
    logo: state.login.logo,
  };
};

export default connect(mapStateToProps)(Partner);
