import React, { useEffect, useState } from "react";
import MyCard from "../../../UI/MyCard/MyCard";
import { adjustTextSize } from "../../../Utility/responsiveTextSize";
import { numberWithComma } from "../../../Utility/numbersWithComma";
import "./home.css";
import { Col } from "reactstrap";
import { axiosInstance } from "../../../Utility/axiosInstance";
import PHomeLoader from "./PHomeLoader/PHomeLoader";
import Row from "reactstrap/lib/Row";
import Progress from "../../../UI/Progress/progress";
import { getTerminalsOfWeek } from "../../../Utility/dateFunctions";
import OverAllGraph from "./OverAllGraph/OverAllGraph";
import StoreGraph from "./StoreGraph/StoreGraph";

const Home = (props) => {
  const [data, setData] = useState({
    total: 0,
    storesVisits: 0,
    mostVisited: 0,
    leastVisited: 0,
    mostVisitedStore: "",
    leastVisitedStore: "",
    storePrev: 0,
  });
  const [partnerChartData, setPartnerChartData] = useState({
    footfall: [],
    visits: [],
  });
  const [partnerChartLoading, setPartnerChartLoading] = useState(false);
  const [storeChartData, setStoreChartData] = useState({
    visits: [],
    storeName: "",
    storeId: "",
    allStores: [],
  });
  const [storeChartLoading, setStoreChartLoading] = useState(false);
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    adjustTextSize();
    setLoading(true);
    axiosInstance
      .post("/partnerdashboard", { time: Date.now() })
      .then((res) => {
        console.log(res.data);
        setData({ ...res.data });

        setLoading(false);
      })
      .catch((err) => {
        console.log(err);
        setLoading(false);
        setData((prev) => ({ ...prev }));
      })
      .finally(() => {
        adjustTextSize();
        getPartnerChartData();
      });
  }, []);

  const getPartnerChartData = () => {
    setPartnerChartLoading(true);
    axiosInstance
      .post("/partner/graph", {
        start: getTerminalsOfWeek(Date.now())[0],
        end: getTerminalsOfWeek(Date.now())[2],
      })
      .then((res) => {
        console.log(res.data);
        setPartnerChartLoading(false);
        setPartnerChartData({ ...res.data });
      })
      .catch((err) => {
        console.log(err);
        setPartnerChartLoading(false);
        setPartnerChartData((prev) => ({ ...prev }));
      })
      .finally(() => {
        getStoreChartData();
      });
  };

  const getStoreChartData = () => {
    setStoreChartLoading(true);
    axiosInstance
      .post("partner/graph/store", {
        start: getTerminalsOfWeek(Date.now())[0],
        end: getTerminalsOfWeek(Date.now())[2],
      })
      .then((res) => {
        console.log(res.data);
        setStoreChartLoading(false);
        setStoreChartData({ ...res.data });
      })
      .catch((err) => {
        console.log(err);
        setStoreChartLoading(false);
        setStoreChartData((prev) => ({ ...prev }));
      });
  };

  const getElement = (value) => {
    if (value === "" || value === undefined || value === null) {
      return (
        <p className="inc-dec-detail" style={{ color: "green" }}>
          0% increase since yesterday
        </p>
      );
    } else {
      return (
        <p
          className="inc-dec-detail"
          style={{
            color: parseInt(value) >= 0 ? "green" : "red",
          }}
        >
          {parseInt(value) > 0 ? "+ " : parseInt(value) === 0 ? "" : ""}
          {value}{" "}
          {parseInt(value) >= 0
            ? "increased since yesterday"
            : "decreased since yesterday"}
        </p>
      );
    }
  };

  // const gatherStoreData = ({ _id }) => {
  //   setChartData((prev) => {
  //     let updated = prev.find((el) => el._id === _id);
  //     return [...prev, { ...updated, name: "Fetching..." }];
  //   });
  //   axiosInstance.post("/getstoregraph", { id: _id }).then((res) => {
  //     console.log(res.data);
  //   });
  // };

  return loading || partnerChartLoading || storeChartLoading ? (
    <PHomeLoader />
  ) : (
    <div>
      <MyCard className="d-flex justify-content-between partner-header hover-grow">
        <div className="heading-steps">
          <h4 className="mobile-heading">
            <span className="red">Today</span>
            <br /> <h4>Overall Footfall</h4>
          </h4>
        </div>
        <div className="heading-number relative">
          <h2 className="resize text-right break-word flex-column">
            <div className="dummy-guy"></div>
            {numberWithComma(data.total)}
          </h2>
        </div>
      </MyCard>
      <br />
      <Row className="bg-white hover-grow dashboard-details-container">
        <Col
          xl="6"
          lg="12"
          md="12"
          sm="12"
          xs="12"
          style={{ position: "relative" }}
        >
          <OverAllGraph chartData={partnerChartData} />
          <br />
        </Col>
        <Col xl="6" lg="12" md="12" sm="12" xs="12">
          <div className="progress-bars-container">
            <div className="progress-bar-container flex-column">
              <div className="each-progress-info">
                <p>Total Store Visits</p>
                <p className="bold">{numberWithComma(data.storesVisits)}</p>
              </div>
              {getElement(data.storePrev)}
              <Progress
                outerClassName="progress-bar"
                innerStyle={{ background: "green" }}
                progress="10"
              />
            </div>
            <div className="progress-bar-container flex-column">
              <div className="each-progress-info">
                <p>Most Visited Store</p>
                <p className="bold">{numberWithComma(data.mostVisited)}</p>
              </div>
              <div>{data.mostVisitedStore}</div>
              <Progress
                outerClassName="progress-bar"
                innerStyle={{ background: "green" }}
                progress="10"
              />
            </div>
            <div className="progress-bar-container flex-column">
              <div className="each-progress-info">
                <p>Least Visited Store</p>
                <p className="bold">{numberWithComma(data.leastVisited)}</p>
              </div>
              <div>{data.leastVisitedStore}</div>
              <Progress
                outerClassName="progress-bar"
                innerStyle={{ background: "green" }}
                progress="10"
              />
            </div>
          </div>
        </Col>
      </Row>
      <br />
      <Row className="bg-white hover-grow dashboard-details-container">
        <Col xl="6" lg="12" md="12" sm="12" xs="12">
          <div className="progress-bars-container">
            <div className="progress-bar-container flex-column">
              <div className="each-progress-info">
                <p>Total Store Visits</p>
                <p className="bold">{numberWithComma(data.storesVisits)}</p>
              </div>
              {getElement(data.storePrev)}
              <Progress
                outerClassName="progress-bar"
                innerStyle={{ background: "green" }}
                progress="10"
              />
            </div>
            <div className="progress-bar-container flex-column">
              <div className="each-progress-info">
                <p>Most Visited Store</p>
                <p className="bold">{numberWithComma(data.mostVisited)}</p>
              </div>
              <div>{data.mostVisitedStore}</div>
              <Progress
                outerClassName="progress-bar"
                innerStyle={{ background: "green" }}
                progress="10"
              />
            </div>
            <div className="progress-bar-container flex-column">
              <div className="each-progress-info">
                <p>Least Visited Store</p>
                <p className="bold">{numberWithComma(data.leastVisited)}</p>
              </div>
              <div>{data.leastVisitedStore}</div>
              <Progress
                outerClassName="progress-bar"
                innerStyle={{ background: "green" }}
                progress="10"
              />
            </div>
          </div>
        </Col>
        <Col
          xl="6"
          lg="12"
          md="12"
          sm="12"
          xs="12"
          style={{ position: "relative" }}
        >
          <StoreGraph chartData={storeChartData} />
          <br />
        </Col>
      </Row>
    </div>
  );
};

export default Home;

