import React from "react";
import { Fragment } from "react";
import MyCard from "../../../../../UI/MyCard/MyCard";
import "./SAddOfferLoader.css";

const SAddOfferLoader = (props) => {
  return (
    <div>
      {props.fromPartner ? (
        <Fragment>
          <MyCard className="full-width flex-center partner-view_offer-title">
            <div className="store-name-title-loader skeleton-box"></div>
          </MyCard>
          <br />
        </Fragment>
      ) : null}
      <MyCard className="flex-column">
        <div className="store-name-title-loader skeleton-box"></div>
        <br />
        <div className="offer-image-loader skeleton-box"></div>
        <br />
        <div className="upload-button-loader skeleton-box"></div>
        <br />
        <div className="add-offer-textbox-loader skeleton-box"></div>
        <br />
        <div className="add-offer-textbox-loader skeleton-box"></div>
        <br />
        <div className="add-offer-textbox-loader skeleton-box"></div>
        <br />
        <div className="add-offer-textbox-loader skeleton-box"></div>
        <br />
        <div className="upload-button-loader skeleton-box"></div>
      </MyCard>
    </div>
  );
};

export default SAddOfferLoader;
