import React, { Fragment, useEffect, useState } from "react";
import { withRouter } from "react-router";
import AsyncButton from "../../../../UI/AsyncButton/AsyncButton";
import MyCard from "../../../../UI/MyCard/MyCard";
import Spinner from "../../../../UI/Spinner/Spinner";
import { axiosInstance } from "../../../../Utility/axiosInstance";
import SAddOfferLoader from "../AddOffer/SAddOfferLoader/SAddOfferLoader";
import EachOffer from "./EachOffer.js/EachOffer";
import "./ViewOffer.css";

const ViewOffer = (props) => {
  const [formData, setFormData] = useState([]);
  const [loading, setLoading] = useState(false);
  const [edit, setEdit] = useState({ display: false, data: {}, index: "" });

  const onEdit = (index) => {
    setEdit({ display: true, data: formData[index], index: index });
  };

  const onEditCancel = () => {
    setEdit({ display: false, data: {}, index: "" });
  };

  useEffect(() => {
    fetchData();
  }, []);

  const fetchData = () => {
    setLoading(true);
    axiosInstance
      .post("/viewstoreoffer", {
        storeId: props.fromPartner ? props.data._id : null,
      })
      .then((res) => {
        console.log(res.data);
        setLoading(false);
        setFormData([
          ...res.data.map((el) => ({
            ...el,
            offerStart: new Date(el.offerStart),
            offerEnd: new Date(el.offerEnd),
          })),
        ]);
      })
      .catch((err) => {
        console.log(err);
        setLoading(false);
      });
  };

  console.log(props.data);

  return loading ? (
    <Fragment>
      <SAddOfferLoader />
      <br />
      <SAddOfferLoader />
      <br />
      <SAddOfferLoader />
      <br />
    </Fragment>
  ) : formData.length === 0 ? (
    <MyCard className="flex-column">
      {props.fromPartner ? (
        <div onClick={props.goBack}>
          <i className="red fa fa-chevron-left pointer" aria-hidden="true"></i>
        </div>
      ) : null}

      <div className="flex-center flex-column">
        <br />
        <h4>No Offers !</h4>
        <br />
        <AsyncButton onClick={() => props.history.replace("/addoffer")}>
          Add New Offer
        </AsyncButton>
      </div>
    </MyCard>
  ) : edit.display ? (
    <EachOffer
      fromPartner={props.fromPartner}
      data={edit.data}
      index={edit.index}
      edit
      entireValue={formData}
      onEdit={onEditCancel}
      afterSubmit={fetchData}
    />
  ) : (
    <Fragment>
      {props.fromPartner ? (
        <MyCard className="full-width flex-row partner-view_offer-title">
          <div className="back-button flex-center" onClick={props.goBack}>
            <i className="red fa fa-chevron-left" aria-hidden="true"></i>
          </div>
          <div className="each-partner-title flex-center">
            <h4 className="break-word">{props.data.name}</h4>
          </div>
        </MyCard>
      ) : null}
      {formData.map((el, index) => (
        <EachOffer
          fromPartner={props.fromPartner}
          entireValue={formData}
          onEdit={onEdit}
          key={el._id}
          index={index}
          edit={false}
          data={el}
        />
      ))}
    </Fragment>
  );
};

export default withRouter(ViewOffer);
