import React, { useEffect, useRef, useState } from "react";
import { Col } from "reactstrap";
import Spinner from "../../../UI/Spinner/Spinner";
import MyCard from "../../../UI/MyCard/MyCard";
import { axiosInstance } from "../../../Utility/axiosInstance";
import "./ViewStore.css";
import { adjustTextSize } from "../../../Utility/responsiveTextSize";
import LoadMoreSpinner from "../../../UI/LoadMoreSpinner/LoadMoreSpinner";
import AsyncButton from "../../../UI/AsyncButton/AsyncButton";
import ViewStoreLoader from "./viewStoreLoader/viewStoreLoader";

var skip = 0,
  count = 1;

const ViewStore = (props) => {
  const [details, setDetails] = useState([]);
  const [loading, setLoading] = useState(false);
  const [showMoreLoading, setShowMoreLoading] = useState(false);
  const loadMoreRef = useRef();
  const [showMoreDisabled, setShowMoreDisabled] = useState(false);

  useEffect(() => {
    // window.addEventListener("scroll", () => {
    //   if (loadMoreRef.current !== null && loadMoreRef.current !== undefined) {
    //     if (
    //       loadMoreRef.current.getBoundingClientRect().bottom >=
    //       window.innerHeight
    //     ) {
    //       // window.scrollBy(0, loadMoreRef.current.getBoundingClientRect().top);
    //       setTimeout(() => {
    //         loadMore();
    //       }, 500);
    //     }
    //   }
    // });

    // $(window).on("scroll", function () {
    //   //page height
    //   var scrollHeight = $(document).height();
    //   //scroll position
    //   var scrollPos = $(window).height() + $(window).scrollTop();
    //   // fire if the scroll position is 300 pixels above the bottom of the page
    //   if ((scrollHeight - 300 >= scrollPos) / scrollHeight == 0) {
    //     loadMore();
    //   }
    // });

    adjustTextSize();
    getData(true);
    return () => {
      skip = 0;
      count = 1;
    };
  }, []);

  const getData = (initial) => {
    console.log("_ ");
    if (count === 1 || !initial) {
      if (initial) setLoading(true);
      else setShowMoreLoading(true);
      axiosInstance
        .post("/viewstore", { skip: skip })
        .then((res) => {
          console.log(res.data);
          setLoading(false);
          setShowMoreLoading(false);
          if (res.data.length >= 0 && res.data.length < 6) {
            setShowMoreDisabled(true);
          }
          if (!initial) {
            setTimeout(() => {
              if (res.status === 200) {
                setDetails(
                  (prev) => {
                    let newArray = [...prev];
                    res.data.map((el) => {
                      newArray.push(el);
                    });
                    return newArray;
                  }
                  // removeDupicatesFromArrayOfObjects([...prev, ...res.data, "_id"])
                );
              }
            }, 500);
          } else {
            if (res.status === 200) {
              setDetails(
                (prev) => {
                  let newArray = [...prev];
                  res.data.map((el) => {
                    newArray.push(el);
                  });
                  return newArray;
                }
                // removeDupicatesFromArrayOfObjects([...prev, ...res.data, "_id"])
              );
              // setDetails(
              //   (prev) => [...res.data]
              //   // removeDupicatesFromArrayOfObjects([...prev, ...res.data, "_id"])
              // );
            }
          }
        })
        .catch((err) => {
          setLoading(false);
          setShowMoreLoading(false);
          setDetails((prev) => [...prev]);
          // console.log(err);
        });
      count = count + 1;
    }
  };

  const loadMore = () => {
    skip = skip + 6;
    getData();
  };

  const gotoStore = (id) => {
    props.history.push({
      pathname: `/viewstore/${id}`,
      state: {
        storeId: id,
      },
    });
  };

  return (
    <div>
      {loading ? (
        // <Spinner />
        <ViewStoreLoader />
      ) : details.length === 0 ? (
        <div className="flex-center flex-column">
          <h4 className="margin-auto text-center">
            <br />
            No Stores !
          </h4>
          <br />
          <AsyncButton onClick={() => props.history.replace("/view")}>
            Add Store
          </AsyncButton>
        </div>
      ) : (
        // <h4>No Stores !</h4>
        <div className="stores-container">
          {details.map((el, index) => (
            <Col
              key={index}
              md="6"
              lg="6"
              sm="12"
              onClick={() => gotoStore(el.storeId)}
            >
              <MyCard className="margin-5 hover-shrink relative pointer each-store">
                <div className="flex-center flex-column">
                  <div
                    className="partner-logo"
                    style={{
                      backgroundImage:
                        "url(" +
                        (el.logo
                          ? el.logo
                          : require("../../../../assets/images/Default-Profile.png")) +
                        ")",
                    }}
                  ></div>

                  <h3 className="resize adjust break-word flex-column flex-center">
                    <h4 style={{ marginTop: 10 }}>{el.name}</h4>

                    <div
                      className="flex-center flex-row"
                      style={{ marginLeft: -20, marginTop: 5 }}
                    >
                      <i
                        className="material-icons"
                        style={{ fontSize: 25, marginTop: -9, color: "red" }}
                      >
                        &#xe0c8;
                      </i>
                      <h4 className="break-word">{el.country}</h4>
                    </div>
                  </h3>
                </div>
              </MyCard>
            </Col>
          ))}
          {/* <a href="#" ref={loadMoreRef} style={{ visibility: "hidden" }}></a> */}

          <div className="margin-auto ease">
            <br />
            {showMoreLoading ? (
              <LoadMoreSpinner />
            ) : (
              <button
                onClick={loadMore}
                disabled={showMoreDisabled}
                className="margin-auto eas  bg-green"
              >
                Show More...
              </button>
            )}
          </div>
        </div>
      )}
    </div>
  );
};

export default ViewStore;
