import React, { Fragment, useState } from "react";
import EachField from "../../../../../UI/FormField/FormField";
import MyCard from "../../../../../UI/MyCard/MyCard";
import { axiosInstance } from "../../../../../Utility/axiosInstance";
import "./EachOffer.css";
import Alert from "../../../../../UI/Alert/Alert";
import AsyncButton from "../../../../../UI/AsyncButton/AsyncButton";
import ImageUpload from "../../../../../UI/ImageUpload/ImageUpload";
import FormInfo from "../../../../../UI/FormInfo/FormInfo";
import Modal from "../../../../../UI/Modal/Modal";
import Spinner from "../../../../../UI/Spinner/Spinner";
import CheckBox from "../../../../../UI/CheckBox/CheckBox";
import SAddOfferLoader from "../../AddOffer/SAddOfferLoader/SAddOfferLoader";

const EachOffer = (props) => {
  const [formData, setFormData] = useState({ ...props.data });
  const [modalLoading, setModalLoading] = useState(false);
  const [options, setOptions] = useState([]);
  const [message, setMessage] = useState("");
  const [info, setInfo] = useState(" ");
  const [type, setType] = useState("");
  const [show, setShow] = useState(false);
  const [loading, setLoading] = useState(false);
  const [submitLoading, setSubmitLoading] = useState(false);
  const [showDropDown, setShowDropDown] = useState(false);

  const getUsers = () => {
    setModalLoading(true);
    axiosInstance
      .post(
        "/listusers",
        props.fromPartner
          ? { storeId: props.data._id }
          : { userId: props.data.userId }
      )
      .then((res) => {
        setModalLoading(false);
        setOptions([...res.data]);
        console.log(res.data);
      })
      .catch((err) => {
        setModalLoading(false);
        setOptions([]);
        console.log(err);
      });
  };

  //   useEffect(() => {
  //     setSchema();
  //   }, [formData]);

  const storeChange = (element) => {
    let included = formData.users.some((el) => el._id === element._id);
    if (!included)
      setFormData((prev) => ({
        ...prev,
        users: [...prev.users, element],
      }));
    else {
      setFormData((prev) => ({
        ...prev,
        users: [...prev.users.filter((el) => el._id !== element._id)],
      }));
    }
  };

  const changeHandler = (event) => {
    const { name, value } = event.target;
    if (name === "startDate") {
      setFormData((prev) => ({
        ...prev,
        [name]: value,
        offerEnd: "",
      }));
    } else
      setFormData((prev) => ({
        ...prev,
        [name]: value,
      }));
  };

  // const selectChangeHandler = (value, name) => {
  //   setFormData((prev) => ({
  //     ...prev,
  //     [name]: value,
  //   }));
  // };

  const submitHandler = (event) => {
    event.preventDefault();
    setSubmitLoading(true);
    let editedObject = {
      ...formData,
      offerStart: formData.offerStart.getTime(),
      offerEnd: formData.offerEnd.getTime(),
    };

    let submitData = [
      ...props.entireValue.map((el) => {
        if (el._id === formData._id) {
          return editedObject;
        }
        return el;
      }),
    ];
    console.log(submitData);
    axiosInstance
      .post("/addstoreoffer", submitData)
      .then((res) => {
        setSubmitLoading(false);
        console.log(res);
        if (res.status === 200) {
          setShow(true);
          setMessage("Success");
          setType("success");
        }
      })
      .catch((err) => {
        setSubmitLoading(false);
        // console.log(err.response.status);
        // setError(getStatus(err.response.status));
        setShow(true);
        setMessage("Error");
        setType("error");
      });
  };

  const valid = () => {
    let requiredFeilds = [
      ...Object.values(formData).map((el) => {
        if (el === null || el === []) {
          return "";
        }
        return el;
      }),
    ];
    return requiredFeilds.every((el) => el.toString().trim() !== "");
  };

  var schema = [
    {
      name: "users",
      disabled: !props.edit,
      style: { cursor: "pointer" },
      displayName: "Store",
      placeholder: "Select Store",
      required: true,
      value: formData.users.map((el) => el.phone + ", "),
      onChange: () => {},
      onClick: () => {
        setShowDropDown(true);
        getUsers();
      },
    },
    {
      name: "offerStart",
      disabled: !props.edit,
      displayName: "Start Date",
      placeholder: "Select Start Date",
      minDate: new Date(),
      type: "date",
      value: formData.offerStart,
      onChange: changeHandler,
      required: true,
      autoComplete: "off",
    },
    {
      name: "offerEnd",
      displayName: "End Date",
      placeholder: "Select End Date",
      minDate:
        formData.offerStart !== "" &&
        formData.offerStart !== null &&
        formData.offerStart !== undefined
          ? new Date(
              formData.offerStart.getFullYear(),
              formData.offerStart.getMonth(),
              formData.offerStart.getDate() + 1
            )
          : null,
      type: "date",
      disabled: formData.offerStart === "" || !props.edit,
      value: formData.offerEnd,
      onChange: changeHandler,
      autoComplete: "off",
      required: true,
    },
  ];

  const hideAlert = () => {
    setShow(false);
    if (type !== "error") {
      props.afterSubmit();
      //   setFormData({
      //     offerStart: "",
      //     offerEnd: "",
      //     offerImage: null,
      //     users: [],
      //   });
    }
  };

  console.log(formData);

  return loading ? (
    <SAddOfferLoader />
  ) : (
    <Fragment>
      <Modal
        className="blurred-box"
        show={showDropDown}
        onClose={() => setShowDropDown(false)}
      >
        <MyCard className="store-dropdown-card bg-transparent">
          <div className="store-dropdown-selector">
            {modalLoading ? (
              <div className="flex-center _100">
                <Spinner />
              </div>
            ) : (
              <div>
                {options.map((el) => (
                  <div
                    key={el._id}
                    className="d-flex justify-content-between flex-wrap each-store-option"
                    style={{ cursor: "pointer" }}
                    onClick={() => storeChange(el)}
                    id={el._id}
                  >
                    <div className="store-name-div vertical-flex-center">
                      {el.phone}
                    </div>
                    <div className="checkbox-div flex-center">
                      <CheckBox
                        name={el.phone}
                        id={el._id}
                        checked={formData.users.some(
                          (ele) => ele._id === el._id
                        )}
                        onChange={() => storeChange(el)}
                      />
                    </div>
                  </div>
                ))}
              </div>
            )}
          </div>
          <AsyncButton type="button" onClick={() => setShowDropDown(false)}>
            Done
          </AsyncButton>
        </MyCard>
      </Modal>

      <div>
        <MyCard title={props.data.storeName} style={{ marginTop: 20 }}>
          <Alert
            type={type}
            message={message}
            hideAlert={hideAlert}
            show={show}
          />
          <form className="form" onSubmit={submitHandler}>
            <ImageUpload
              needPreview
              sizeLimit="1"
              compress
              className="my-image"
              editable={props.edit}
              image={formData.offerImage}
              onChange={(imageurl, validity, error) => {
                setInfo(error);
                if (validity) {
                  setInfo(" ");
                  setFormData((prev) => ({
                    ...prev,
                    offerImage: imageurl,
                  }));
                } else {
                  setFormData((prev) => ({
                    ...prev,
                    offerImage: null,
                  }));
                }
              }}
            />
            <FormInfo info={info} />
            {schema.map((el, index) => (
              <EachField key={index} {...el} />
            ))}
            {/* <FormInfo info={error} /> */}
            <div className="flex-row">
              {props.edit ? (
                <AsyncButton
                  loading={submitLoading}
                  disabled={!valid()}
                  className="bg-green lg edit-options"
                  type="submit"
                >
                  Update
                </AsyncButton>
              ) : null}
              <AsyncButton
                type="button"
                onClick={() => props.onEdit(props.index)}
                style={{ background: props.edit ? "#ee3632" : "#4fb664" }}
                className="bg-green lg edit-options"
              >
                {props.edit ? "Cancel" : "Edit"}
              </AsyncButton>
            </div>
          </form>
        </MyCard>
      </div>
    </Fragment>
  );
};

export default EachOffer;
