import React, { Fragment } from "react";
import { Col } from "reactstrap";
import Row from "reactstrap/lib/Row";
import MyCard from "../../../../UI/MyCard/MyCard";
import "./PHomeLoader.css";

const PHomeLoader = (props) => {
  return (
    <Fragment>
      <MyCard className="d-flex justify-content-between partner-header">
        <div className="left-container">
          <div className="left-total skeleton-box"></div>
          <div className="left-total-2 skeleton-box"></div>
        </div>
        <div className="right-side skeleton-box"></div>
      </MyCard>
      <br />
      <Row className="bg-white hover-grow dashboard-details-container">
        <Col xl="6" lg="12" md="12" sm="12" xs="12">
          <div className="graph-loader-container">
            <br />
            <div className="chart-header">
              <div className="chart-title-loader skeleton-box"></div>
              <div className="chart-dd-loader skeleton-box"></div>
            </div>
            <div className="graph-store-name-loader skeleton-box"></div>
            <br />
            <div className="graph-loader skeleton-box"></div>
          </div>
          <br />
        </Col>
        <Col xl="6" lg="12" md="12" sm="12" xs="12">
          <div className="progress-bars-container">
            <div className="progress-bar-container flex-column">
              <div className="each-progress-info">
                <p className="visit-details-name skeleton-box"></p>
                <p className="visit-details-count-loader skeleton-box"></p>
              </div>
              <p className="visit-details-inc-dec skeleton-box"></p>
              <div className="progress-bar-loader skeleton-box"></div>
            </div>
            <br />
            <div className="progress-bar-container flex-column">
              <div className="each-progress-info">
                <p className="visit-details-name skeleton-box"></p>
                <p className="visit-details-count-loader skeleton-box"></p>
              </div>
              <p className="visit-details-inc-dec skeleton-box"></p>
              <div className="progress-bar-loader skeleton-box"></div>
            </div>
            <br />
            <div className="progress-bar-container flex-column">
              <div className="each-progress-info">
                <p className="visit-details-name skeleton-box"></p>
                <p className="visit-details-count-loader skeleton-box"></p>
              </div>
              <p className="visit-details-inc-dec skeleton-box"></p>
              <div className="progress-bar-loader skeleton-box"></div>
            </div>
          </div>
        </Col>
      </Row>
    </Fragment>
  );
};

export default PHomeLoader;
