import React from "react";
import { Col } from "reactstrap";
import MyCard from "../../../../UI/MyCard/MyCard";
import "./ProfileLoader.css";
const ProfileLoader = (props) => {
  return (
    <MyCard className="flex-center wrap reverse-at-mobile">
      <Col lg="6" md="6" sm="12">
        <div className="profile-edit-button-loader skeleton-box"></div>
        <br />
        <div className="form">
          <br />
          <div className="skeleton-box field-label-loader"></div>
          <div className="profile-field-loader skeleton-box"></div>
          <br />
          <br />
          <div className="skeleton-box field-label-loader"></div>
          <div className="profile-field-loader skeleton-box"></div>
          <br />
          <br />
          <div className="skeleton-box field-label-loader"></div>
          <div className="profile-field-loader skeleton-box"></div>
          <br />
        </div>
      </Col>
      <Col
        lg="6"
        md="6"
        sm="12"
        className="flex-center adjust-onwrap flex-column"
      >
        <div className="profile">
          <div className="profile-page-logo skeleton-box"></div>
        </div>
      </Col>
    </MyCard>
  );
};

export default ProfileLoader;
