import React, { useEffect, useState } from "react";
import { Col } from "reactstrap";
import Alert from "../../../../UI/Alert/Alert";
import AsyncButton from "../../../../UI/AsyncButton/AsyncButton";
import EachField from "../../../../UI/FormField/FormField";
import MyCard from "../../../../UI/MyCard/MyCard";
import { axiosInstance } from "../../../../Utility/axiosInstance";
import "./EachStore.css";
import EachStoreLoader from "./EachStoreLoader/EachStoreLoader";

const EachStore = (props) => {
  const [details, setDetails] = useState({
    name: "",
    email: "",
    address: "",
    QRCode: null,
  });

  const [edit, setEdit] = useState(false);

  const [show, setShow] = useState(false);
  const [message, setMessage] = useState("");
  const [type, setType] = useState("success");
  const [loading, setLoading] = useState(false);
  const [submitLoading, setSubmitLoading] = useState(false);

  const changeHandler = (event) => {
    const { name, value } = event.target;
    setDetails((prev) => ({
      ...prev,
      [name]: value,
    }));
  };
  var schema = [
    {
      name: "name",
      displayName: "Name",
      type: "text",
      value: details.name,
      onChange: changeHandler,
      spellCheck: false,
      required: true,
    },
    {
      name: "email",
      displayName: "Email",
      type: "email",
      value: details.email,
      onChange: changeHandler,
      required: true,
      spellCheck: false,
    },
    {
      name: "address",
      displayName: "Address",
      type: "textarea",
      value: details.address,
      onChange: changeHandler,
      required: true,
      spellCheck: false,
    },
  ];

  useEffect(() => {
    setLoading(true);
    axiosInstance
      .post("/viewsinglestore", { storeId: props.match.params.id })
      .then((res) => {
        setLoading(false);
        if (res.status === 200) {
          setDetails((prev) => ({ ...prev, ...res.data }));
        } else {
          setDetails((prev) => ({
            ...prev,
            name: "",
            email: "",
            address: "",
            QRCode: null,
          }));
        }
        console.log(res.data);
      })
      .catch((err) => {
        setLoading(false);
        setDetails((prev) => ({
          ...prev,
          name: "",
          email: "",
          address: "",
          QRCode: null,
        }));
        console.log(err);
        props.history.replace("/viewstore");
      });
  }, []);

  const togglEdit = () => {
    setEdit((prev) => !prev);
  };

  const submitHandler = (event) => {
    event.preventDefault();
    // setSubmitLoading(true)
    // axiosInstance.post('/u')
  };

  const valid = () => {
    let requiredFeilds = [...Object.values(details)];
    return requiredFeilds.every((el) => el.trim() !== "");
  };

  const hideAlert = () => {
    setShow(false);
  };

  console.log(props.match.params.id);

  return (
    <div>
      {loading ? (
        <EachStoreLoader />
      ) : details.length === 0 ? (
        <h4 className="margin-auto text-center">
          <br />
          <br />
          Something went Wrong !
        </h4>
      ) : (
        <MyCard className="flex-row wrap">
          <Col lg="6" md="6" sm="12" className="flex-column">
            <div
              className="partner-logo"
              style={{
                backgroundImage:
                  "url(" +
                  (details.logo
                    ? details.logo
                    : require("../../../../../assets/images/Default-Profile.png")) +
                  ")",
              }}
            ></div>
            <div>
              <br />
              <Alert
                type={type}
                message={message}
                hideAlert={hideAlert}
                show={show}
              />
              {edit ? (
                <form className="form" onSubmit={submitHandler}>
                  {schema.map((el, index) => (
                    <EachField key={index} {...el} />
                  ))}
                  <AsyncButton
                    loading={submitLoading}
                    disabled={!valid()}
                    className="bg-green lg"
                    type="submit"
                  >
                    Update
                  </AsyncButton>
                </form>
              ) : (
                <div className="form">
                  {schema.map((el, index) => (
                    <EachField key={index} {...el} disabled={true} />
                  ))}
                </div>
              )}
            </div>
          </Col>
          <Col lg="6" md="6" sm="12" className="flex-center">
            <div className="qr-code">
              {details.QRCode === null ? (
                <div className="bg-grey _100"></div>
              ) : (
                <img src={details.QRCode} alt=" " width="100%" height="100%" />
              )}
            </div>
          </Col>
        </MyCard>
      )}
    </div>
  );
};

export default EachStore;
