import React, { useEffect, useRef, useState } from "react";
import { Col } from "reactstrap";
import Alert from "../../../UI/Alert/Alert";
import AsyncButton from "../../../UI/AsyncButton/AsyncButton";
import EachField from "../../../UI/FormField/FormField";
import FormInfo from "../../../UI/FormInfo/FormInfo";
import MyCard from "../../../UI/MyCard/MyCard";
import Spinner from "../../../UI/Spinner/Spinner";
import { axiosInstance } from "../../../Utility/axiosInstance";
import { compressImage } from "../../../Utility/CompressImage";
import { validateImage } from "../../../Utility/validateImage";
import "./Profile.css";
import ProfileLoader from "./ProfileLoader/ProfileLoader";

const Profile = (props) => {
  const [details, setDetails] = useState({
    name: "",
    email: "",
    address: "",
    logo: require("../../../../assets/images/Default-Profile.png"),
  });

  const [detailsCopy, setDetailsCopy] = useState({
    name: "",
    email: "",
    address: "",
    logo: require("../../../../assets/images/Default-Profile.png"),
  });

  const edit = useRef();

  const [editProfile, setEditProfile] = useState(false);
  const [sending, setSending] = useState(false);
  const [show, setShow] = useState(false);
  const [message, setMessage] = useState("");
  const [type, setType] = useState("success");
  const [loading, setLoading] = useState(false);
  const [submitLoading, setSubmitLoading] = useState(false);
  const [validity, setValidity] = useState(false);
  const [touched, setTouched] = useState(false);
  const [imageUploading, setImageUploading] = useState(false);
  const [imageError, setImageError] = useState(" ");
  const [errorColor, setErrorColor] = useState("coral");

  const changeHandler = (event) => {
    const { name, value } = event.target;
    setDetails((prev) => ({
      ...prev,
      [name]: value,
    }));
  };

  var schema = [
    {
      name: "name",
      displayName: "Name",
      placeholder: editProfile ? "Enter Name" : "",
      type: "text",
      value: details.name,
      onChange: changeHandler,
      required: true,
      spellCheck: false,
    },
    {
      name: "email",
      displayName: "Email",
      placeholder: editProfile ? "Enter Email" : "",
      type: "email",
      value: details.email,
      disabled: true,
      onChange: changeHandler,
      required: true,
      spellCheck: false,
    },
    {
      name: "address",
      displayName: "Address",
      placeholder: editProfile ? "Enter Address" : "",
      type: "textarea",
      value: details.address,
      onChange: changeHandler,
      spellCheck: false,
    },
  ];

  useEffect(() => {
    getData();
  }, []);

  const getData = () => {
    setLoading(true);
    axiosInstance
      .post("/partnerprofile")
      .then((res) => {
        setLoading(false);
        if (res.status === 200) {
          setDetails((prev) => ({ ...prev, ...res.data }));
          setDetailsCopy((prev) => ({ ...prev, ...res.data }));
        } else {
          setDetails((prev) => ({
            ...prev,
            name: "",
            email: "",
            address: "",
            logo: require("../../../../assets/images/Default-Profile.png"),
          }));
          setDetailsCopy((prev) => ({
            ...prev,
            name: "",
            email: "",
            address: "",
            logo: require("../../../../assets/images/Default-Profile.png"),
          }));
        }
        console.log(res);
      })
      .catch((err) => {
        setLoading(false);
        setDetails((prev) => ({
          ...prev,
          name: "",
          email: "",
          address: "",
          logo: require("../../../../assets/images/Default-Profile.png"),
        }));
        setDetailsCopy((prev) => ({
          ...prev,
          name: "",
          email: "",
          address: "",
          logo: require("../../../../assets/images/Default-Profile.png"),
        }));
      });
  };

  const submitHandler = (event) => {
    event.preventDefault();
    setSubmitLoading(true);
    axiosInstance
      .post("/updatepartnerprofile", details)
      .then((res) => {
        console.log(res.data);
        setSubmitLoading(false);
        if (res.status === 200) {
          setMessage("Profile updated !");
          setShow(true);
          setType("success");
        }
      })
      .catch((err) => {
        setSubmitLoading(false);
        setMessage("Profile Not updated !");
        setShow(true);
        setType("error");
      });
  };

  const valid = () => {
    let requiredFeilds = [details.name, details.email];
    return requiredFeilds.every((el) => el.trim() !== "");
  };

  const hideAlert = () => {
    setShow(false);
    setEditProfile(false);
    setImageError(" ");
    setErrorColor("coral");
    if (type !== "error") getData();
  };

  const imageChangeHandler = async (e) => {
    setTouched(true);
    setImageError(" ");
    if (e.target.files[0] !== undefined) {
      setImageUploading(true);
      setValidity(validateImage(e.target.files[0].name));
      if (!validateImage(e.target.files[0].name)) {
        setImageError("Choose only .jpeg / .jpg / .png / .jfif / .webp");
        setValidity(false);
        return;
      } else if (e.target.files[0].size > 2000 * 1000) {
        setImageError("Size should be within 2MB");
        setValidity(false);
        return;
      } else {
        setSending(true);
        setErrorColor("green");
        setImageError("Your image is being processed...");

        const callBack = (image) => {
          let processedImage = image[0].prefix + image[0].data;
          setDetails((prev) => ({ ...prev, logo: processedImage }));
          setImageUploading(false);
          updateProfile(processedImage);
        };
        await compressImage([e.target.files[0]], { size: 0.5 }, callBack);
      }
    } else {
      setValidity(false);
      setImageError("No file Choosen");
    }
  };

  const updateProfile = (image) => {
    setImageError("Uploading...");
    axiosInstance
      .post("/updatepartnerprofile", { ...detailsCopy, logo: image })
      .then((res) => {
        setImageError(" ");
        console.log(res.data);
        setSending(false);
        if (res.status === 200) {
          setMessage("Profile updated !");
          setShow(true);
          setType("success");
        }
      })
      .catch((err) => {
        setDetails((prev) => ({
          ...prev,
          logo: detailsCopy.logo,
        }));
        setSending(false);
        setImageError("Something went Wrong !");
        setMessage("Profile Not updated !");
        setShow(true);
        setType("error");
      });
  };

  const imageEdit = () => {
    edit.current.click();
  };

  const togglEdit = () => {
    if (edit) {
      setDetails((prev) => ({ ...detailsCopy, logo: prev.logo }));
    }
    setEditProfile((prev) => !prev);
  };

  return (
    <div>
      {loading ? (
        <ProfileLoader />
      ) : details.length === 0 ? (
        <h4 className="margin-auto text-center">
          <br />
          <br />
          Something went Wrong !
        </h4>
      ) : (
        <MyCard className="flex-center wrap reverse-at-mobile">
          <Col lg="6" md="6" sm="12">
            <AsyncButton
              className={"lg" + (editProfile ? " bg-red" : " bg-green")}
              type="button"
              onClick={togglEdit}
            >
              {editProfile ? "Cancel" : "Edit"}
            </AsyncButton>
            <br />
            <Alert
              type={type}
              message={message}
              hideAlert={hideAlert}
              show={show}
            />
            {editProfile ? (
              <form className="form" onSubmit={submitHandler}>
                {schema.map((el, index) => (
                  <EachField key={index} {...el} />
                ))}
                <AsyncButton
                  loading={submitLoading}
                  disabled={!valid()}
                  className="bg-green lg"
                  type="submit"
                >
                  Update
                </AsyncButton>
              </form>
            ) : (
              <div className="form">
                {schema.map((el, index) => (
                  <EachField key={index} {...el} disabled={true} />
                ))}
                <AsyncButton
                  loading={submitLoading}
                  disabled
                  type="button"
                  style={{ visibility: "hidden" }}
                >
                  Update
                </AsyncButton>
              </div>
            )}
          </Col>
          <Col
            lg="6"
            md="6"
            sm="12"
            className="flex-center adjust-onwrap flex-column"
          >
            <div className="profile">
              <input
                ref={edit}
                type="file"
                style={{ position: "absolute", visibility: "hidden" }}
                onChange={imageChangeHandler}
              />
              {details.logo === null || (!validity && touched) ? (
                <div
                  style={{
                    backgroundImage:
                      "url(" +
                      require("../../../../assets/images/Default-Profile.png") +
                      ")",
                  }}
                  alt=" "
                  className={`profile-page-logo ${
                    sending ? "skeleton-loading" : ""
                  }`}
                >
                  <div onClick={sending ? () => {} : () => imageEdit()}>
                    <p>Edit</p>
                  </div>
                </div>
              ) : (
                <div
                  style={{
                    backgroundImage: "url(" + details.logo + ")",
                  }}
                  alt=" "
                  className={`profile-page-logo ${
                    sending ? "skeleton-loading" : ""
                  }`}
                >
                  <div onClick={sending ? () => {} : () => imageEdit()}>
                    <p>Edit</p>
                  </div>
                </div>
              )}
            </div>
            <FormInfo info={imageError} color={errorColor} />
          </Col>
        </MyCard>
      )}
    </div>
  );
};

export default Profile;

