import React from "react";
import { Line } from "@reactchartjs/react-chart.js";
import "./chart.css";

const MultiAxisLine = ({
  heading,
  name,
  footfall = null,
  visits,
  ToggleComponent = null,
}) => {
  const data = (canvas) => {
    const ctx = canvas.getContext("2d");
    // const visitsGradient = ctx.createLinearGradient(0, 0, 0, canvas.offsetHeight);
    // visitsGradient.addColorStop(0, "#fd6967");
    // visitsGradient.addColorStop(1, "#ffd0cf");

    // const footfallsGradient = ctx.createLinearGradient(
    //   0,
    //   0,
    //   0,
    //   canvas.offsetHeight
    // );
    // footfallsGradient.addColorStop(0, "#36a2eb");
    // footfallsGradient.addColorStop(1, "#a3d6f8");
    return {
      labels: ["Sun", "Mon", "Tue", "Wed", "Thu", "Fri", "Sat"],
      datasets: [
        {
          label: "Visits",
          data: [...visits],
          fill: false,
          backgroundColor: "#fd6967",
          borderColor: "#fd6967",
          borderWidth: 3,
          yAxisID: "y-axis-1",
          pointRadius: 0,
        },
        footfall && {
          label: "Footfalls",
          data: [...footfall],
          fill: false,
          backgroundColor: "#36a2eb",
          borderColor: "#36a2eb",
          borderWidth: 3,
          pointRadius: 0,
          yAxisID: "y-axis-1",
        },
      ].filter((el) => el !== undefined && el !== null),
    };
  };

  const options = {
    scales: {
      yAxes: [
        {
          ticks: {
            beginAtZero: true,
          },
          type: "linear",
          display: true,
          position: "left",
          id: "y-axis-1",
        },
      ],
    },
  };

  return (
    <>
      <div className="header">
        <br />
        <div className="chart-header">
          <h1 className="title">{heading}</h1>
          <ToggleComponent />
        </div>
        <div className="links">
          <a
            className="btn btn-gh"
            // href="https://github.com/reactchartjs/react-chartjs-2/blob/react16/example/src/charts/MultiAxisLine.js"
          >
            {name}
          </a>
        </div>
      </div>
      <Line data={data} options={options} />
    </>
  );
};

export default MultiAxisLine;
