import React, { useState } from "react";
import EachField from "../../../UI/FormField/FormField";
import MyCard from "../../../UI/MyCard/MyCard";
import { axiosInstance } from "../../../Utility/axiosInstance";
import "./AddStore.css";
import Alert from "../../../UI/Alert/Alert";
import AsyncButton from "../../../UI/AsyncButton/AsyncButton";

const AddStore = (props) => {
  const [formData, setFormData] = useState({
    name: "",
    address: "",
    contactPerson: "",
    email: "",
    contactNumber: "",
  });
  const [message, setMessage] = useState("");
  const [type, setType] = useState("");
  const [show, setShow] = useState(false);
  const [loading, setLoading] = useState(false);

  const changeHandler = (event) => {
    const { name, value } = event.target;
    setFormData((prev) => ({
      ...prev,
      [name]: value,
    }));
  };

  // const selectChangeHandler = (value, name) => {
  //   setFormData((prev) => ({
  //     ...prev,
  //     [name]: value,
  //   }));
  // };

  var schema = [
    {
      name: "name",
      displayName: "Store Name",
      placeholder: "Enter Store Name",
      type: "text",
      value: formData.name,
      onChange: changeHandler,
      required: true,
    },
    {
      name: "address",
      displayName: "Address",
      placeholder: "Enter Address",
      type: "textarea",
      value: formData.address,
      onChange: changeHandler,
      required: true,
    },
    {
      name: "contactPerson",
      displayName: "Contact Person",
      placeholder: "Enter Contact Person",
      type: "text",
      value: formData.contactPerson,
      onChange: changeHandler,
      required: true,
    },
    {
      name: "email",
      displayName: "Email",
      placeholder: "Enter Email",
      type: "email",
      value: formData.email,
      onChange: changeHandler,
      required: true,
    },
    {
      name: "contactNumber",
      displayName: "Contact Number",
      placeholder: "Enter Contact Number",
      type: "number",
      value: formData.contactNumber,
      onChange: changeHandler,
      required: true,
    },
  ];

  const submitHandler = (event) => {
    event.preventDefault();
    setLoading(true);
    axiosInstance
      .post("/signup/store", formData)
      .then((res) => {
        setLoading(false);
        console.log(res);
        if (res.status === 200) {
          setShow(true);
          setMessage("Success");
          setType("success");
        }
      })
      .catch((err) => {
        setLoading(false);
        console.log(err.response.status);
        // setError(getStatus(err.response.status));
        setShow(true);
        setMessage("Error");
        setType("error");
      });
  };

  const valid = () => {
    let requiredFeilds = [...Object.values(formData)];
    return requiredFeilds.every((el) => el.trim() !== "");
  };

  const hideAlert = () => {
    setShow(false);
    if (type !== "error") {
      setFormData({
        name: "",
        address: "",
        contactPerson: "",
        email: "",
        contactNumber: "",
      });
    }
  };

  return (
    <div>
      <MyCard title="ADD NEW STORE">
        <Alert
          type={type}
          message={message}
          hideAlert={hideAlert}
          show={show}
        />
        <form className="form" onSubmit={submitHandler}>
          {schema.map((el, index) => (
            <EachField key={index} {...el} />
          ))}
          {/* <FormInfo info={error} /> */}
          <AsyncButton
            loading={loading}
            disabled={!valid()}
            className="bg-green lg"
            type="submit"
          >
            ADD
          </AsyncButton>
        </form>
      </MyCard>
    </div>
  );
};

export default AddStore;
