import React, { Fragment, useEffect, useRef, useState } from "react";
import EachField from "../../../../UI/FormField/FormField";
import MyCard from "../../../../UI/MyCard/MyCard";
import { axiosInstance } from "../../../../Utility/axiosInstance";
import "./AddOffer.css";
import Alert from "../../../../UI/Alert/Alert";
import AsyncButton from "../../../../UI/AsyncButton/AsyncButton";
import ImageUpload from "../../../../UI/ImageUpload/ImageUpload";
import FormInfo from "../../../../UI/FormInfo/FormInfo";
import Modal from "../../../../UI/Modal/Modal";
import CheckBox from "../../../../UI/CheckBox/CheckBox";
import { withRouter } from "react-router";
import SAddOfferLoader from "./SAddOfferLoader/SAddOfferLoader";

const AddOffer = (props) => {
  const [formData, setFormData] = useState({
    offerStart: "",
    offerEnd: "",
    offerImage: null,
    users: [],
  });
  const [options, setOptions] = useState([]);
  const [message, setMessage] = useState("");
  const [info, setInfo] = useState(" ");
  const [type, setType] = useState("");
  const [show, setShow] = useState(false);
  const [loading, setLoading] = useState(false);
  const [submitLoading, setSubmitLoading] = useState(false);
  const [showDropDown, setShowDropDown] = useState(false);

  useEffect(() => {
    setLoading(true);
    axiosInstance
      .post(props.fromPartner ? "/listuserspartner" : "/listusers")
      .then((res) => {
        setLoading(false);
        setOptions([...res.data]);
        console.log(res.data);
      })
      .catch((err) => {
        setLoading(false);
        setOptions([]);
        console.log(err);
      });
  }, []);

  const storeChange = (element) => {
    let included = formData.users.some((el) => el._id === element._id);
    if (!included)
      setFormData((prev) => ({
        ...prev,
        users: [...prev.users, element],
      }));
    else {
      setFormData((prev) => ({
        ...prev,
        users: [...prev.users.filter((el) => el._id !== element._id)],
      }));
    }
  };

  const changeHandler = (event) => {
    const { name, value } = event.target;
    if (name === "startDate") {
      setFormData((prev) => ({
        ...prev,
        [name]: value,
        offerEnd: "",
      }));
    } else
      setFormData((prev) => ({
        ...prev,
        [name]: value,
      }));
  };

  // const selectChangeHandler = (value, name) => {
  //   setFormData((prev) => ({
  //     ...prev,
  //     [name]: value,
  //   }));
  // };

  var schema = [
    {
      name: "users",
      style: { cursor: "pointer" },
      displayName: "Users",
      placeholder: "Select Users",
      required: true,
      value: formData.users.map((el) => el.phone + ", "),
      onChange: () => {},
      onClick: () => setShowDropDown(true),
      autoComplete: "off",
    },
    {
      name: "offerStart",
      displayName: "Start Date",
      placeholder: "Select Start Date",
      minDate: new Date(),
      type: "date",
      value: formData.offerStart,
      onChange: changeHandler,
      required: true,
      autoComplete: "off",
    },
    {
      name: "offerEnd",
      displayName: "End Date",
      placeholder: "Select End Date",
      minDate:
        formData.offerStart !== "" &&
        formData.offerStart !== null &&
        formData.offerStart !== undefined
          ? new Date(
              formData.offerStart.getFullYear(),
              formData.offerStart.getMonth(),
              formData.offerStart.getDate() + 1
            )
          : null,
      type: "date",
      disabled: formData.offerStart === "",
      value: formData.offerEnd,
      onChange: changeHandler,
      autoComplete: "off",
      required: true,
    },
  ].filter((el) => el !== null);

  const submitHandler = (event) => {
    event.preventDefault();
    console.log(props.data.storeId);
    setSubmitLoading(true);
    let submitData = {
      ...formData,
      storeId: props.fromPartner ? props.data.storeId : null,
      offerStart: formData.offerStart.getTime(),
      offerEnd: formData.offerEnd.getTime(),
    };
    console.log(submitData);
    axiosInstance
      .post(
        props.fromPartner ? "/addpartneroffer" : "/addstoreoffer",
        submitData
      )
      .then((res) => {
        setSubmitLoading(false);
        console.log(res);
        if (res.status === 200) {
          setShow(true);
          setMessage("Success");
          setType("success");
        }
      })
      .catch((err) => {
        setSubmitLoading(false);
        // console.log(err.response.status);
        // setError(getStatus(err.response.status));
        setShow(true);
        setMessage("Error");
        setType("error");
      });
  };

  const valid = () => {
    let requiredFeilds = [
      ...Object.values(formData).map((el) => {
        if (el === null || el === []) {
          return "";
        }
        return el;
      }),
    ];
    return requiredFeilds.every((el) => el.toString().trim() !== "");
  };

  const hideAlert = () => {
    setShow(false);
    if (type !== "error") {
      setFormData({
        offerStart: "",
        offerEnd: "",
        offerImage: null,
        users: [],
      });
    }
  };

  return loading ? (
    <SAddOfferLoader fromPartner={props.fromPartner} />
  ) : (
    <Fragment>
      {props.fromPartner ? (
        <MyCard className="full-width flex-row partner-view_offer-title">
          <div className="back-button flex-center" onClick={props.goBack}>
            <i className="red fa fa-chevron-left" aria-hidden="true"></i>
          </div>
          <div className="each-partner-title flex-center">
            <h4 className="break-word">{props.data.name}</h4>
          </div>
        </MyCard>
      ) : null}
      <br />
      <Modal
        className="blurred-box"
        show={showDropDown}
        onClose={() => setShowDropDown(false)}
      >
        <MyCard className="store-dropdown-card bg-transparent">
          <div className="store-dropdown-selector bg-transparent">
            {options.length === 0 ? (
              <div className="flex-center">
                <h4>No Users !</h4>
              </div>
            ) : (
              <div>
                {options.map((el) => (
                  <div
                    key={el._id}
                    className="d-flex justify-content-between flex-wrap each-store-option"
                    style={{ cursor: "pointer" }}
                    onClick={() => storeChange(el)}
                    id={el._id}
                  >
                    <div className="store-name-div vertical-flex-center">
                      {el.phone}
                    </div>
                    <div className="checkbox-div flex-center">
                      <CheckBox
                        name={el.phone}
                        id={el._id}
                        checked={formData.users.some(
                          (ele) => ele._id === el._id
                        )}
                        onChange={() => storeChange(el)}
                      />
                    </div>
                  </div>
                ))}
              </div>
            )}
          </div>
          <AsyncButton type="button" onClick={() => setShowDropDown(false)}>
            Done
          </AsyncButton>
        </MyCard>
      </Modal>

      <div>
        <MyCard title="ADD OFFER">
          <Alert
            type={type}
            message={message}
            hideAlert={hideAlert}
            show={show}
          />
          <form className="form" onSubmit={submitHandler}>
            <ImageUpload
              needPreview
              sizeLimit="1"
              compress
              className="my-image"
              image={formData.offerImage}
              onChange={(imageurl, validity, error) => {
                setInfo(error);
                if (validity) {
                  setInfo(" ");
                  setFormData((prev) => ({
                    ...prev,
                    offerImage: imageurl,
                  }));
                } else {
                  setFormData((prev) => ({
                    ...prev,
                    offerImage: null,
                  }));
                }
              }}
            />
            <FormInfo info={info} />
            {schema.map((el, index) => (
              <EachField key={index} {...el} />
            ))}
            {/* <FormInfo info={error} /> */}
            <AsyncButton
              loading={submitLoading}
              disabled={!valid()}
              className="bg-green lg"
              type="submit"
            >
              ADD
            </AsyncButton>
          </form>
        </MyCard>
      </div>
    </Fragment>
  );
};

export default withRouter(AddOffer);
