import React, { Fragment, useEffect, useRef, useState } from "react";
import { Col } from "reactstrap";
import MyCard from "../../../../UI/MyCard/MyCard";
import { axiosInstance } from "../../../../Utility/axiosInstance";
import "./ViewOfferPartner.css";
import { adjustTextSize } from "../../../../Utility/responsiveTextSize";
import LoadMoreSpinner from "../../../../UI/LoadMoreSpinner/LoadMoreSpinner";
import AsyncButton from "../../../../UI/AsyncButton/AsyncButton";
import ViewOffer from "../../../Store/Offers/ViewOffer/ViewOffer";
import PAddOfferLoader from "../AddOfferPartner/PAddOfferLoader/PAddOfferLoader";

var skip = 0,
  count = 1;

const ViewOfferPartner = (props) => {
  const [details, setDetails] = useState([]);
  const [loading, setLoading] = useState(false);
  const [showMoreLoading, setShowMoreLoading] = useState(false);
  const [showMoreDisabled, setShowMoreDisabled] = useState(false);
  const [show, setShow] = useState({ display: false, data: {} });

  useEffect(() => {
    adjustTextSize();
    getData(true);
    return () => {
      skip = 0;
      count = 1;
    };
  }, []);

  const getData = (initial) => {
    console.log("_ ");
    if (count === 1 || !initial) {
      if (initial) setLoading(true);
      else setShowMoreLoading(true);
      axiosInstance
        .post("/viewstore", { skip: skip })
        .then((res) => {
          // console.log(res.data);
          setLoading(false);
          setShowMoreLoading(false);
          if (res.data.length >= 0 && res.data.length < 6) {
            setShowMoreDisabled(true);
          }
          if (!initial) {
            setTimeout(() => {
              if (res.status === 200) {
                setDetails(
                  (prev) => {
                    let newArray = [...prev];
                    res.data.map((el) => {
                      newArray.push(el);
                    });
                    return newArray;
                  }
                  // removeDupicatesFromArrayOfObjects([...prev, ...res.data, "_id"])
                );
              }
            }, 500);
          } else {
            if (res.status === 200) {
              setDetails((prev) => {
                let newArray = [...prev];
                res.data.map((el) => {
                  newArray.push(el);
                });
                return newArray;
              });
            }
          }
        })
        .catch((err) => {
          console.log(err);
          setLoading(false);
          setShowMoreLoading(false);
          setDetails((prev) => [...prev]);
        });
      count = count + 1;
    }
  };

  const loadMore = () => {
    skip = skip + 6;
    getData();
  };

  const gotoAddOffer = (data) => {
    setShow({ display: true, data: { ...data } });
  };
  const goBack = () => {
    console.log("hello");
    setShow({ display: false, data: {} });
  };

  return (
    <div>
      {loading ? (
        <PAddOfferLoader />
      ) : details.length === 0 ? (
        <MyCard className="flex-column">
          <div onClick={goBack}>
            <i className="red fa fa-chevron-left" aria-hidden="true"></i>
          </div>

          <div className="flex-center flex-column">
            <h4 className="margin-auto text-center">
              <br />
              No Stores !
            </h4>
            <br />
            <AsyncButton onClick={() => props.history.replace("/view")}>
              Add Store
            </AsyncButton>
          </div>
        </MyCard>
      ) : !show.display ? (
        <Fragment>
          <MyCard className="full-width partner-view_offer-title">
            <div className="each-partner-title flex-center">
              <h4 className="break-word">Choose Store</h4>
            </div>
          </MyCard>
          <br />

          <div className="stores-container-partner">
            {details.map((el, index) => (
              <Col
                key={index}
                md="6"
                lg="6"
                sm="12"
                onClick={() => gotoAddOffer(el)}
              >
                <MyCard className="margin-5 hover-shrink relative pointer each-store">
                  <div className="flex-center flex-column">
                    <div
                      className="partner-logo-partner"
                      style={{
                        backgroundImage:
                          "url(" +
                          (el.logo
                            ? el.logo
                            : require("../../../../../assets/images/Default-Profile.png")) +
                          ")",
                      }}
                    ></div>

                    <h3 className="resize adjust-partner break-word flex-column flex-center">
                      <h4 style={{ marginTop: 10 }}>{el.name}</h4>

                      <div
                        className="flex-center flex-row"
                        style={{ marginLeft: -20, marginTop: 5 }}
                      >
                        <i
                          className="material-icons"
                          style={{ fontSize: 25, marginTop: -9, color: "red" }}
                        >
                          &#xe0c8;
                        </i>
                        <h4 className="break-word">{el.country}</h4>
                      </div>
                    </h3>
                  </div>
                </MyCard>
              </Col>
            ))}

            <div className="margin-auto ease">
              <br />
              {showMoreLoading ? (
                <LoadMoreSpinner />
              ) : (
                <button
                  onClick={loadMore}
                  disabled={showMoreDisabled}
                  className="margin-auto eas  bg-green"
                >
                  Show More...
                </button>
              )}
            </div>
          </div>
        </Fragment>
      ) : (
        <ViewOffer goBack={goBack} data={show.data} fromPartner />
      )}
    </div>
  );
};

export default ViewOfferPartner;
