import React, { Fragment, useEffect, useState } from "react";
import MyDropDown from "../../../../UI/DropDown/DropDown";
import WindowsLoader from "../../../../UI/WindowsLoader/WindowsLoader";
import { axiosInstance } from "../../../../Utility/axiosInstance";
import {
  calculateDate,
  getDateCollapsed,
  getTerminalsOfWeek,
} from "../../../../Utility/dateFunctions";
import MultiAxisLine from "../Chart/chart";
import "./StoreGraph.css";

const StoreGraph = ({ chartData }) => {
  const [overallGraphLoading, setOverallGraphLoading] = useState(false);
  const [newChartData, setNewChartData] = useState({ ...chartData });
  const [error, setError] = useState("");
  const [currentWeekOption, setCurrentWeekOption] = useState({
    // label: "Last 7 days",
    // value: [
    //   calculateDate(Date.now(), -7),
    //   getTerminalsOfWeek(Date.now())[1],
    //   calculateDate(Date.now()),
    // ],
    label: "This Week",
    value: getTerminalsOfWeek(Date.now()),
  });

  useEffect(() => {
    setNewChartData({ ...chartData });
  }, [chartData]);

  const storesToggler = () => {
    return (
      <div className="dropdown-container">
        <MyDropDown
          disabled={overallGraphLoading}
          placeholder={
            <div className="dropdown-component-label flex-column">
              <small>{currentWeekOption.label}</small>
            </div>
          }
          options={[
            {
              name: (
                <div className="dropdown-component flex-column">
                  <p>This Week</p>
                  {/* <p>Last 7 days</p> */}
                  <small>
                    ({getDropdownLabel(Date.now())}
                    {/* {getDateCollapsed(calculateDate(Date.now(), -7)) +
                      " -- " +
                      getDateCollapsed(Date.now())} */}
                    )
                  </small>
                </div>
              ),
              onClick: () =>
                graphDateToggle(
                  Date.now(),
                  "This Week",
                  // getTerminalsOfWeek(Date.now())[1],
                  //   "Last 7 days",
                  true
                ),
            },
            {
              name: (
                <div className="dropdown-component flex-column">
                  <p>Last Week</p>
                  <small>
                    (
                    {getDropdownLabel(
                      calculateDate(currentWeekOption.value[1], -7)
                    )}
                    )
                  </small>
                </div>
              ),
              onClick: () =>
                graphDateToggle(
                  calculateDate(currentWeekOption.value[1], -7, 0, 0),
                  getDropdownLabel(
                    calculateDate(currentWeekOption.value[1], -7, 0, 0)
                  )
                ),
            },
            {
              name: (
                <div className="dropdown-component flex-column">
                  <p>Last Month</p>
                  <small>
                    (
                    {getDropdownLabel(
                      calculateDate(currentWeekOption.value[1], 0, -1)
                    )}
                    )
                  </small>
                </div>
              ),
              onClick: () =>
                graphDateToggle(
                  calculateDate(currentWeekOption.value[1], 0, -1),
                  getDropdownLabel(
                    calculateDate(currentWeekOption.value[1], 0, -1)
                  )
                ),
            },
            {
              name: (
                <div className="dropdown-component flex-column">
                  <p>Last Year</p>
                  <small>
                    (
                    {getDropdownLabel(
                      calculateDate(currentWeekOption.value[1], 0, 0, -1)
                    )}
                    )
                  </small>
                </div>
              ),
              onClick: () =>
                graphDateToggle(
                  calculateDate(currentWeekOption.value[1], 0, 0, -1),
                  getDropdownLabel(
                    calculateDate(currentWeekOption.value[1], 0, 0, -1)
                  )
                ),
            },
          ]}
        />
        &nbsp;&nbsp;
        <MyDropDown
          disabled={overallGraphLoading}
          placeholder={
            <div className="dropdown-component-label flex-column">
              <small>{newChartData.storeName}</small>
            </div>
          }
          options={[
            ...chartData.allStores.map((el, index) => ({
              name: (
                <div className="dropdown-component flex-column">
                  <p>{el.name}</p>
                </div>
              ),
              onClick: () => graphStoreToggle(el.storeId, el.name),
            })),
          ]}
        />
      </div>
    );
  };

  const getDropdownLabel = (timestamp) => {
    return (
      getDateCollapsed(getTerminalsOfWeek(timestamp)[0]) +
      " -- " +
      getDateCollapsed(getTerminalsOfWeek(timestamp)[2])
    );
  };

  const graphDateToggle = (newDate, newLabel, lastSevenDays) => {
    let dates =
      // lastSevenDays
      //   ? [
      //       calculateDate(Date.now(), -7),
      //       getTerminalsOfWeek(Date.now())[1],
      //       calculateDate(Date.now()),
      //     ]
      //   :
      getTerminalsOfWeek(newDate);
    // if (dates[0].getDate() !== currentOption.value[0].getDate()) {
    setOverallGraphLoading(true);
    axiosInstance
      .post("/partner/graph/store", {
        storeId: newChartData.storeId,
        start: dates[0],
        end: dates[2],
      })
      .then((res) => {
        console.log(res.data);
        setCurrentWeekOption((prev) => ({
          ...prev,
          label: newLabel,
          value: dates,
        }));
        setOverallGraphLoading(false);
        setNewChartData({ ...res.data });
      })
      .catch((err) => {
        console.log(err);
        setOverallGraphLoading(false);
        setError("Something is wrong !");
        setCurrentWeekOption((prev) => ({ ...prev }));
      });
    // }
  };
  const graphStoreToggle = (storeId, storeName) => {
    let dates = currentWeekOption.value;
    // if (dates[0].getDate() !== currentOption.value[0].getDate()) {
    setOverallGraphLoading(true);
    axiosInstance
      .post("/partner/graph/store", {
        storeId: storeId,
        start: dates[0],
        end: dates[2],
      })
      .then((res) => {
        console.log(res.data);
        setOverallGraphLoading(false);
        setNewChartData({ ...res.data });
      })
      .catch((err) => {
        console.log(err);
        setOverallGraphLoading(false);
        setError("Something is wrong !");
      });
    // }
  };
  return (
    <Fragment>
      <div className="graph-loading-container">
        {overallGraphLoading ? <WindowsLoader /> : null}
      </div>
      <MultiAxisLine
        heading="Stores Report (Weekly)"
        name={
          getDateCollapsed(currentWeekOption.value[0]) +
          " -- " +
          getDateCollapsed(currentWeekOption.value[2])
        }
        ToggleComponent={storesToggler}
        {...newChartData}
      />
    </Fragment>
  );
};

export default StoreGraph;
// /partner/graph/store
